#workspace #getstarted {
  margin-left: 10px; }
  #workspace #getstarted img {
    display: block;
    width: 5%;
    margin-top: 80px;
    vertical-align: bottom; }
  #workspace #getstarted div {
    margin: 0 0 0 8%; }
  #workspace #getstarted div#instructions {
    margin-top: -15px; }
  #workspace #getstarted div#video {
    margin-top: 2em; }

#workspace .row {
  margin: 0; }
  #workspace .row .cb-XMLFile {
    background: Grey;
    border: 1px solid White;
    min-height: 20em;
    padding: 2px; }
    #workspace .row .cb-XMLFile .cb-xf-title {
      min-height: 2em;
      color: white;
      font-weight: bold; }
      #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls {
        float: right; }
        #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls a:link, #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls a:hover, #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls a:active, #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls a:visited {
          color: white;
          text-decoration: none; }
        #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls button span {
          display: none; }
      #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-xp-drawer {
        color: black;
        width: 250px;
        background-color: #AFCCE6;
        height: 35px;
        margin: auto;
        position: absolute;
        top: 36px;
        left: 0;
        right: 0;
        z-index: 999 !important;
        text-align: center;
        border-radius: 5px;
        display: none; }
        #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-xp-drawer span {
          display: inline-block;
          margin-top: 5px; }
        #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-xp-drawer span.cb-xf-xp-d-ctrls {
          display: inline-block;
          float: right;
          margin: 5px 10px 0 0; }
      #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-xp-drawer.cb-xp-ok {
        background-color: green; }
      #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-xp-drawer.cb-xp-fail {
        background-color: red; }
    #workspace .row .cb-XMLFile .cb-ace {
      min-height: inherit; }

@media (min-width: 500px) {
  #workspace .row .cb-XMLFile .cb-xf-title .cb-xf-controls button span {
    display: inline; } }
